@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;500;600;700&display=swap');

.header{
      display: flex;
      align-items: center;
      background-color: black ;
      top: 0;
      position: sticky;
      z-index: 2000;
}

.header_left{
    flex: 0.4;

}
.header_left >h1{
    text-align: center;
    font-family: "Alata", sans-serif;
    letter-spacing: 1px;
    padding: 20px 0;
    color: azure;

}
.header_left >h1>span{
    color: #f64c08;
}

.header_right{
    display :flex;
    flex: 0.6;
    justify-content: space-evenly;
    align-items: center;
}

.header_right >a>h5{
    color: ghostwhite;
    font-size: 14px;
    cursor: pointer;
    transition: transform 300ms ease-in-out;
    font-family: "Crimson Text",sans-serif;
    padding: 20px 0;
    letter-spacing: 1px;
}

.header_right >a>h5:hover{
    transform: scale(1.09);
    background-color: #f64c08;
    color: ghostwhite;
    padding: 5px 20px;
    border-radius: 2px;
    letter-spacing: 1px;
    font-family: "Crimson Text",sans-serif;
}
.header_rightButton{
    background-color: #f64c08;
    color: ghostwhite;
    padding: 5px 20px !important;
    border-radius: 2px;
    cursor : pointer;
}