@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap');

.topcontent{
    color:white;
    position: relative;
}

.topcontent_about{
    position: absolute;
    top : 300px;
    right:50px;
}

.topcontent_about >p{
    letter-spacing: 1px;
    text-align: left;
    padding-left: 3px;
    padding-top: 20px;


}

.topcontent_about>a>button{
    margin-top: 30px;
    text-align: left;
    margin-left: 5px;
    border: none;
    border-radius: 2px;
    outline: none;
}

.topcontent_about>h1{
    font-family: "Monserrat",sans-serif;
    font-size: 50px;
}

.topcontent_downloadcv{
    color: white;
    background-color:  #f64c08;
    font-size: 14px;
    padding: 10px 30px;
    font-family: "Montserrat",sans-serif;
    font-weight: 300;
    cursor: pointer;
    margin-right: 20px;
}

.topcontent_downloadcv:hover{
    color: #f64c08;
    background-color: white;
    font-weight: 600px;

}

.topcontent_myworks{
    color: white;
    border:1px solid white !important;
    background-color: transparent;
    font-size: 14px;
    padding: 9px 45px;
    font-family: "Montserrat",sans-serif;
    cursor:pointer;

}
.topcontent_myworks:hover{
    color: black;
    background-color: white;
    font-weight: 600px;
}